.modal {
  display: none;
  position: fixed;
  z-index: 99;
  inset: 0;
  align-items: center;
  justify-content: center;
  background-color: rgba(37, 35, 33, 0.80);
  @include mobile {
    padding-top: 32px;
  }
  &-form {

    @include mobile {
      padding-top: 0;
    }

  }
  &__window {
    position: relative;
    z-index: 10;
    width: 90%;
    height: 90%;
    border: 1px solid var(--focus-color);
    background-color: #d3d3d34f;
    @include mobile {
      height: 80%;
      overflow-y: auto;
    }
    iframe {
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
    }
    &--form {
      position: relative;
      z-index: 10;
      width: unset;
      height: unset;
      border: none;
      max-height: 100%;
      @include mobile {
        padding: 0 16px;
        overflow-y: auto;
      }
    }
  }
  &.active {
    display: flex;
  }
  &__close {
    position: absolute;
    z-index: 21;
    top: 0;
    right: -10px;
    transform: translateX(100%);
    width: 32px;
    height: 32px;
    border-radius: 4px;
    background-color: var(--focus-color);
    background-image: url('./../img/close-icon.svg');
    background-position: center center;
    background-size: 15px 15px;
    background-repeat: no-repeat;

    transition: background-color .3s ease-in;
    @include mobile {
      top: -50px;
      right: 0;
      transform: translateY(-100%);
    }
    @include hover {
      background-color: var(--focus-active-color);
    }
    @media (min-width: 1440px) {
      width: 3.2rem;
      height: 3.2rem;
      background-size: 1.5rem 1.5rem;
    }
  }
}


.modal-form__form {
  max-width: 830px !important;
  flex-direction: row !important;
  gap: 20px;
  @include mobile {
    flex-direction: column !important;
  }
}
